.tui-ico-prev {
  width: 10px;
  height: 1.4px;
  background-color: #000;
  background-position: 50%;
  background-repeat: no-repeat;
  font-size: 0;
  display: block;
  position: relative;
  top: 20px;
  left: 15px;
}

.tui-ico-prev:before {
  content: "";
  width: 5px;
  height: 1.4px;
  background-color: #000;
  position: absolute;
  top: 2px;
  right: 5px;
  transform: rotate(45deg);
}

.tui-ico-prev:after {
  content: "";
  width: 5px;
  height: 1.4px;
  background-color: #000;
  position: absolute;
  top: -2px;
  right: 5px;
  transform: rotate(-45deg);
}

