    transform: scale(.5);
  }

  50% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.2);
  }
}

@keyframes beat {
  from {
    transform-origin: center;
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  10% {
    animation-timing-function: ease-in;
    transform: scale(1.3);
  }

  15% {
    animation-timing-function: ease-out;
    transform: scale(1.1);
  }

  25% {
    animation-timing-function: ease-in;
    transform: scale(1.08);
  }

  35% {
    animation-timing-function: ease-out;
    transform: scale(1.2);
  }
}

